html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-plane {
  width: 1em;
  height: 1em;
  fill: #333;
}

body {
  background: #FFF;
  line-height: 1.2;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

a {
  text-decoration: none;
}

strong {
  font-weight: 700;
}

.title-lg {
  color: #fff;
  text-align: center;
  font-family: Roboto;
  font-size: 48px;
}

.title-lg, .title-decor, .title-md {
  font-weight: 700;
}

.title-md {
  color: #939393;
  font-size: 30px;
  margin-bottom: 30px;
}

.title-md--green {
  color: #94c34a;
}

.title-m {
  color: #939393;
  font-size: 24px;
  margin-bottom: 25px;
}

.title-decor {
  margin-bottom: 40px;
  font-size: 30px;
  color: #828282;
}

.title-decor:after {
  content: "";
  position: absolute;
  bottom: -6px;
  left: 0;
  width: 51px;
  height: 1px;
  background-color: #464545;
}

.article p {
  margin-bottom: 18px;
}

.article p:last-of-type {
  margin-bottom: 0;
}

.l {
  max-width: 1170px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
}

.block {
  display: flex;
}

.header {
  padding: 0 5px;
  font-size: 0;
  background: #605f5f;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
}

.header__right {
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
}

.header__logo {
  display: inline-block;
  vertical-align: top;
}

.header__logo img {
  max-width: 195px;
  height: auto;
  max-height: 34px;
}

.header__user {
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: 84px;
}

.header__user-inner a, .header__user-inner span {
  font-size: 24px;
  font-weight: 400;
  display: inline-block;
  vertical-align: top;
  color: #fff;
  margin-left: 25px;
}

.header__user-inner a {
  transition: color 0.3s;
}

.header__user-inner a.is-active {
  color: #94c34a;
}

@media only screen and (min-width: 1024px) {
  .header__user-inner a:hover {
    color: #94c34a;
  }
}

.lang-list {
  display: inline-block;
  vertical-align: top;
}

.lang-list img {
  width: 30px;
  height: 20px;
}

.lang-list li {
  display: inline-block;
  vertical-align: top;
  margin-right: 19px;
}

.lang-list li:last-of-type {
  margin-right: 0;
}

.lang-list a {
  display: block;
}

.soc-btn {
  appearance: none;
  background: transparent;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  border: 1px solid #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: border-color 0.3s;
}

.soc-btn svg {
  fill: #fff;
  font-size: 19px;
  transition: fill 0.3s;
}

@media only screen and (min-width: 1024px) {
  .soc-btn:hover {
    border-color: #94c34a;
  }
  .soc-btn:hover svg {
    fill: #94c34a;
  }
}

.contact-btn {
  background-image: url("../img/sprite.png");
  width: 282px;
  height: 59px;
  background-position: 0px 0px;
  appearance: none;
  background-color: transparent;
  border: none;
  text-align: center;
  padding: 14px 20px  14px 40px;
  position: relative;
  top: 15px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.contact-btn span {
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  display: inline-block;
  vertical-align: top;
  margin-right: 50px;
}

.contact-btn svg {
  font-size: 20px;
  fill: #fff;
}

.nav {
  position: relative;
  padding-top: 19px;
  padding-bottom: 22px;
}

.nav__bg {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.nav__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav a {
  display: inline-block;
  vertical-align: top;
  color: #fff;
  font-size: 24px;
  transition: background 0.3s;
  padding: 4px 14px;
  border: 1px solid #fff;
  border-radius: 20px;
}

.nav a.is-active {
  background: #40630a;
}

@media only screen and (min-width: 1024px) {
  .nav a:hover {
    background: #40630a;
  }
}

.footer {
  background: #605f5f;
  padding-top: 25px;
  padding-bottom: 15px;
  font-size: 0;
}

.footer__inner {
  *zoom: 1;
}

.footer__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__left {
  float: left;
  margin-right: 112px;
}

.footer__center {
  float: left;
}

.footer__right {
  float: right;
  text-align: right;
}

.footer__logo {
  max-width: 262px;
  max-height: 45px;
  margin-bottom: 28px;
  display: inline-block;
  vertical-align: top;
}

.footer__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.footer__nav {
  margin-bottom: 35px;
}

.footer__nav li {
  display: inline-block;
  vertical-align: top;
  margin-right: 45px;
}

.footer__nav li:last-of-type {
  margin-right: 0;
}

.footer__nav a {
  font-size: 18px;
  color: #fff;
  transition: color 0.3s;
}

.footer__nav a.is-active {
  color: #94c34a;
  text-decoration: underline;
}

@media only screen and (min-width: 1024px) {
  .footer__nav a:hover {
    color: #94c34a;
  }
}

.copyright {
  color: #000;
  font-size: 14px;
  font-weight: 400;
}

.accept-block {
  font-size: 0;
}

.accept-block p {
  color: #fff;
  font-size: 18px;
  margin-bottom: 24px;
}

.accept-block li {
  display: inline-block;
  vertical-align: top;
  margin-right: 9px;
}

.accept-block li:last-of-type {
  margin-right: 0;
}

.accept-block li img {
  width: 48px;
  height: 48px;
}
